
.footer {
  background-color: rgba(0,0,0,.65);
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  display: flex;
  justify-content: center;
}
.content {
  margin-top: 50px;
  min-height: calc(100vh - 51px);
  height: calc(100vh - 51px);
}
.navlist {
  display: flex;
}
.router-item {
  font-size: 14px;
  padding: 0 20px;
  color: #a3a3a3;
  cursor: pointer;
}
.active {
  color: #1890ff;
}