.motto-wrap {
  height: 100%;
  background-color: #fff;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  .motto-content {
    width: 1300px;
    padding: 0 150px;
    height: 200px;
    font-size: 28px;
    font-weight: 600;
    text-align: center;
  }
  .refresh-btn {
    margin-bottom: 150px;
    padding: 6px 40px;
    font-size: 16px;
    background-color: #096dd9;
    color: #fff;
    border-radius: 20px;
    cursor: pointer;
  }
}